@charset "UTF-8";
.main {
  margin: 0 auto;
  width: 100%;
}

.main-hero {
  width: 100%;
  height: 629px;
  background-image: url("../../assets/bg/impuls-bg.jpg");
  background-position: center center;
  background-size: cover;
  color: var(--font-dark);
}

.main-hero__headers {
  padding-top: 192px;
  margin: 0 auto;
}

.main-hero__head1 {
  font-size: 64px;
  line-height: 150%;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 40px;
  margin-top: 0;
}

.main-hero__head2 {
  font-weight: 500;
  font-size: 36px;
  max-width: 484px;
  line-height: 150%;
}

.main-advantages {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 40px auto 25px;
  margin-top: 40px;
}

.advantages-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
  min-width: 248px;
}

.advantages-item__img {
  width: 50px;
  height: 50px;
}

.main-catalog {
  margin: 0 auto;
}

.catalog-item {
  border: 1px solid var(--card-beige);
}

.catalog-items__top {
  display: grid;
  grid-template-columns: repeat(3, 386px);
  grid-template-rows: repeat(3, 210px);
  justify-items: center;
  gap: 20px 20px;
  grid-template-areas: "A B C" "A D E" "G F E";
}
.catalog-items__top .catalog-item:first-child {
  flex-direction: column-reverse;
  justify-content: space-between;
  padding-top: 130px;
}
.catalog-items__top .catalog-item:first-child .catalog-item__info {
  align-self: flex-start;
}
.catalog-items__top .catalog-item:first-child .catalog-item__image-container img {
  max-width: 270px;
  max-height: 200px;
}
.catalog-items__top .catalog-item:nth-child(1) {
  grid-area: A;
}
.catalog-items__top .catalog-item:nth-child(2) {
  grid-area: B;
}
.catalog-items__top .catalog-item:nth-child(2) .catalog-item__image-container img {
  max-height: 140px;
}
.catalog-items__top .catalog-item:nth-child(3) {
  grid-area: C;
}
.catalog-items__top .catalog-item:nth-child(3) .catalog-item__image-container img {
  max-height: 140px;
}
.catalog-items__top .catalog-item:nth-child(4) {
  grid-area: D;
  gap: 76px;
}
.catalog-items__top .catalog-item:nth-child(4) img {
  max-height: 140px;
}
.catalog-items__top .catalog-item:nth-child(5) {
  grid-area: E;
}
.catalog-items__top .catalog-item:nth-child(5) img {
  max-height: 320px;
}
.catalog-items__top .catalog-item:nth-child(6) {
  grid-area: G;
  flex-direction: column-reverse;
  gap: 0px;
}
.catalog-items__top .catalog-item:nth-child(6) .catalog-item__info {
  align-self: flex-start;
}
.catalog-items__top .catalog-item:nth-child(7) {
  grid-area: F;
  gap: 20px;
}
.catalog-items__top .catalog-item:nth-child(7) img {
  max-height: 150px;
}

.catalog-items__bottom {
  display: flex;
  flex-direction: row;
  margin-top: 20px;
  justify-content: space-between;
}
.catalog-items__bottom .catalog-item {
  width: 285px;
  height: 210px;
}
.catalog-items__bottom .catalog-item:nth-child(1) {
  gap: 15px;
}
.catalog-items__bottom .catalog-item:nth-child(1) img {
  max-height: 150px;
}
.catalog-items__bottom .catalog-item:nth-child(2) {
  flex-direction: column-reverse;
  gap: 0;
}
.catalog-items__bottom .catalog-item:nth-child(2) .catalog-item__info {
  align-self: flex-start;
}
.catalog-items__bottom .catalog-item:nth-child(2) .catalog-item__info h4 {
  margin-top: 10px;
}
.catalog-items__bottom .catalog-item:nth-child(3) {
  gap: 0;
}
.catalog-items__bottom .catalog-item:nth-child(3) img {
  max-height: 150px;
}
.catalog-items__bottom .catalog-item:nth-child(4) {
  flex-direction: column-reverse;
  gap: 0;
}
.catalog-items__bottom .catalog-item:nth-child(4) .catalog-item__info {
  align-self: flex-start;
}
.catalog-items__bottom .catalog-item:nth-child(4) img {
  max-height: 80px;
}

.catalog-item {
  width: 386px;
  display: flex;
  flex-direction: row;
  background-color: #fdf8f5;
  justify-content: flex-start;
  align-items: center;
  padding: 24px;
  gap: 40px;
}
.catalog-item .catalog-item__info {
  align-self: flex-end;
}
.catalog-item .catalog-item__info a {
  text-decoration: none;
}

.catalog-item__image-container {
  max-width: 370px;
}
.catalog-item__image-container img {
  max-width: 200px;
  max-height: 100px;
}

.catalog-item__info h4 {
  margin-bottom: 6px;
}

.main-individual {
  margin: 0 auto;
}

.individual-hero {
  background-image: url("../assets/bg/individual-bg.jpeg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 440px;
  display: flex;
  align-items: center;
  padding: 100px;
  margin-top: 106px;
}
.individual-hero h2 {
  font-size: 48px;
  line-height: 150%;
  color: var(--font-dark);
  font-weight: 600;
  letter-spacing: 0%;
}

.main-news {
  margin: 0 auto;
}

.news-container {
  margin-top: 90px;
  margin-bottom: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}
.news-container h3 {
  font-size: 32px;
  font-weight: 600;
  line-height: 120%;
}

.news-items {
  align-self: flex-start;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 20px;
  flex-wrap: wrap;
}

.news-item {
  max-width: 386px;
  display: flex;
  flex-direction: column;
}

.news-item__title {
  margin: 16px 0;
  font-size: 16px;
  letter-spacing: 0%;
  line-height: 20px;
}

.news-item__info {
  font-size: 12px;
  margin-top: 0;
  line-height: 150.5%;
}

.news-item__link-date {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
  font-size: 12px;
}

.news-item__link,
.news-item__date {
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0%;
  font-size: 12px;
}

.section-contacts {
  margin: 0 auto;
}

.contacts-container {
  margin: 120px auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.contacts-info__head {
  margin-bottom: 50px;
}

.contacts-info__data {
  display: flex;
  flex-direction: column;
  color: var(--font-dark);
}
.contacts-info__data a {
  color: var(--font-dark);
  line-height: 180%;
  /* &:hover {
    color: var(--font-black);
    font-weight: 500;
  } */
}

.contacts-info__social a {
  margin-right: 17px;
}

.main-title {
  background-image: url("../assets/bg/page-title-bg.png");
  background-position: center center;
  padding-bottom: 50px;
  max-height: 216px;
  margin: 0 auto;
}
.main-title .page-title__section {
  margin: 0 auto;
  padding-top: 50px;
}
.main-title .page-title__breadcumbs ul {
  font-family: "Raleway";
  font-size: 14px;
  color: var(--font-dark);
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  list-style: none;
  margin: 0;
}
.main-title .page-title__breadcumbs li:not(:last-child)::after {
  content: "»";
  margin: 0 5px;
}
.main-title h1 {
  text-transform: uppercase;
  font-size: 48px;
  font-weight: 700;
  line-height: 150%;
  margin: 8px 0 0;
}
.main-title p {
  line-height: 180%;
  margin: 0;
}

.about-info__item {
  display: flex;
  margin: 80px auto;
  justify-content: center;
  align-items: center;
}
.about-info__item p {
  line-height: 180%;
}

.about-advantages {
  padding-top: 36px;
  margin-bottom: 60px;
}
.about-advantages .advantages-item {
  flex-direction: column;
  align-items: flex-start;
  gap: 0;
}
.about-advantages .advantages-item:nth-child(2) {
  padding-left: 30px;
}
.about-advantages .advantages-item:nth-child(3) {
  padding-right: 20px;
}
.about-advantages .advantages-item p {
  line-height: 160%;
  margin: 6px 0;
}

.about-clients {
  margin: 0 auto;
  padding-top: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about-clients h2 {
  margin: 0;
}

.about-clients__items {
  display: flex;
  flex-direction: row;
  gap: 21px;
  margin-top: 60px;
}

.stocks-section {
  margin-top: 80px;
}

.stock-items {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0 auto;
  gap: 20px;
  justify-content: space-between;
}

.stock-item {
  height: 440px;
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 37px;
  padding-left: 31px;
}
.stock-item:nth-child(1) {
  width: 683px;
  background-image: url("../assets/stocks/Frame 3542.jpg");
}
.stock-item:nth-child(2) {
  background-image: url("../assets/stocks/Frame 3545.jpg");
  width: 488px;
}
.stock-item:nth-child(3) {
  background-image: url("../assets/stocks/Frame 3546.jpg");
  background-size: cover;
  flex: 1;
}
.stock-item p {
  font-weight: 500;
  margin: 0;
}
.stock-item h4 {
  font-weight: 600;
  margin: 8px 0 16px;
  font-size: 28px;
}

.contacts-page__contacts-container {
  margin-top: 80px;
}

.contacts-page__info {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 500px;
  justify-content: space-between;
  padding-top: 95px;
}
.contacts-page__info p {
  line-height: 180%;
}
.contacts-page__info .contacts-page__info-tel a {
  display: block;
}
.contacts-page__info .contacts-info__social {
  min-width: 180px;
}

.map-iframe__contacts-page {
  width: 590px;
  height: 469px;
}

@media screen and (max-width: 1615px) {
  .mobile-container {
    padding: 0 100px;
  }
  .catalog-items__top {
    grid-template-columns: repeat(3, 1fr);
    width: 100%;
  }
}
@media screen and (max-width: 1420px) {
  .catalog-items__top {
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: "A B" "A C" "D E" "G F";
  }
  .catalog-items__top .catalog-item {
    width: 100%;
  }
  .catalog-items__top .catalog-item:nth-child(5) img {
    max-height: 180px;
  }
  .catalog-items__bottom {
    flex-wrap: wrap;
    gap: 20px;
  }
  .catalog-items__bottom .catalog-item {
    flex: 1;
    height: auto;
    width: 48.5%;
  }
  .news-items {
    justify-content: space-around;
  }
}
@media screen and (max-width: 1150px) {
  .main-hero__head1 {
    font-size: 48px;
  }
  .main-hero__head2 {
    font-size: 24px;
  }
  .main-advantages {
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  .advantages-item {
    flex-direction: column;
  }
  .individual-hero {
    height: 330px;
  }
  .individual-hero h2 {
    font-size: 36px;
  }
  .advantages-item {
    flex-direction: column;
  }
  .contacts-container {
    gap: 20px;
    flex-direction: column;
  }
  .contacts-info {
    flex: 1;
  }
  .contacts-map {
    flex: 1;
  }
  .map-iframe {
    width: 100%;
  }
}
@media screen and (max-width: 920px) {
  .catalog-item {
    flex-direction: column-reverse;
    justify-content: space-between;
    gap: 0;
  }
  .catalog-item .catalog-item__info {
    align-self: flex-start;
  }
  .catalog-items__top {
    grid-template-rows: repeat(3, 255px);
  }
  .catalog-items__top .catalog-item:nth-child(4) {
    gap: 20px;
  }
  .catalog-items__top .catalog-item:nth-child(5) img {
    max-height: 140px;
  }
  .catalog-items__top .catalog-item:nth-child(7) img {
    max-height: 90px;
  }
  .catalog-items__top .catalog-item__image-container {
    max-height: 90px;
  }
  .catalog-items__top .catalog-item__image-container img {
    width: 100%;
    max-height: 90px;
  }
  .catalog-items__bottom .catalog-item {
    flex-direction: column-reverse;
  }
  .catalog-items__bottom .catalog-item:nth-child(1) img {
    max-height: 70px;
  }
  .catalog-items__bottom .catalog-item:nth-child(3) img {
    max-height: 70px;
  }
  .main-individual {
    padding: 0;
  }
  .individual-hero h2 {
    font-size: 24px;
  }
}
@media screen and (max-width: 768px) {
  .container {
    padding: 0 50px;
  }
  .main-hero {
    height: 490px;
  }
  .main-hero__headers {
    padding-top: 150px;
  }
  .main-individual {
    padding: 0;
  }
  .individual-hero {
    padding: 50px;
  }
}
@media screen and (max-width: 480px) {
  .container {
    padding-left: 0;
    padding-right: 0;
  }
  .catalog-items__top,
  .catalog-items__bottom {
    gap: 0;
  }
  .catalog-items__bottom {
    margin-top: 0;
  }
  .contacts-info h3,
  .contacts-info div {
    text-align: center;
  }
}